    //연습
    .projectpop {
        position: relative;
        width: 100%;
        max-width: 1100px;
        height: 100%;
        padding: 80px 0;
        z-index: 1000;
        .popcontent {
            width: 100%;
            height: 100%;
            overflow-y: auto;
            .popclose {
                position: absolute;
                top: 38px;
                right: 0;
                width: 32px;
                height: 32px;
                cursor: pointer;
                z-index: 1000;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .floatingwrapper {
                position: absolute;
                top: 134px;
                right: -80px;
                gap: 24px;
                z-index: 10002;
                .pjicon {
                    display: flex;
                    flex-direction: column;
                    .linkbtn {
                        align-items: center;
                        text-decoration: none;
                        color: white;
                        .iconwrap {
                            width: 40px;
                            height: 40px;
                            background-color: #f5f5f5;
                            border-radius: 50%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            .floatingicon {
                                width: 28px;
                                height: 28px;
                            }
                        }
                        .subtitle1 {
                            margin: 0px;
                        }
                    }
                }
            }

            .inner {
                // width: 100%;
                height: 100%;
                height: calc(100% - 80px);
                overflow-y: auto;
                background-color: #fff;
                border-radius: 8px 8px 0 0;
                padding: 48px 40px;
                scrollbar-width: none; /* Firefox */
                -ms-overflow-style: none;
                &::-webkit-scrollbar {
                    display: none; /* Chrome, Safari, Edge */
                }
                header {
                    // background-color: rgb(240, 252, 252);
                    .header3 {
                        margin-bottom: 40px;
                    }
                    .pimgwrap {
                        width: 100%;
                        height: 510px;
                        margin-bottom: 59px;
                        & img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            // object-position: center top;
                        }
                    }
                }

                .deswrap {
                    // width: 100%;
                    // background-color: rgb(219, 176, 227);
                    // gap: 32px;
                    // justify-content: space-between;
                    // height: 100%;
                    aside {
                        min-width: 220px;
                        // background-color: rgb(170, 244, 135);
                        & p:nth-of-type(1) {
                            color: #ad8957;
                            margin: 0;
                        }
                        .body1,
                        .caption2 {
                            margin: 0;
                            line-height: 24px;
                        }
                        .body1 {
                            width: 178px;
                            margin: 8px 0 16px;
                        }
                    }
                    main {
                        // width: 1051px;
                        .section {
                            width: 100%;

                            // background-color: rgb(124, 148, 253);
                            margin-bottom: 48px;
                            position: relative;
                            &:nth-of-type(2) .pdes ul {
                                background-color: #f5f5f5;
                                border-radius: 8px;
                                padding-top: 8px;
                                padding-bottom: 8px;
                            }
                            &:nth-of-type(2) .pdes ul,
                            &:nth-of-type(3) .pdes ul,
                            &:nth-of-type(4) .pdes ul,
                            &:nth-of-type(5) .pdes ul {
                                padding-left: 30px;
                            }
                            &:nth-of-type(2) .pdes,
                            &:nth-of-type(3) .pdes,
                            &:nth-of-type(4) .pdes,
                            &:nth-of-type(5) .pdes {
                                padding-left: 0px;
                            }

                            .ptitle {
                                margin-bottom: 12px;
                                &::before {
                                    content: "";
                                    display: block;
                                    position: absolute;
                                    width: 3px;
                                    height: 32px;
                                    background-color: #ad8957;
                                    left: 0;
                                    top: 0;
                                }
                                padding-left: 12px;
                            }

                            .pdes {
                                // padding-left: 12px;
                                .screen {
                                    padding: 0 12px;
                                    .flex {
                                        width: 100%;
                                        height: 100%;
                                        justify-content: space-between;
                                        flex-wrap: wrap;
                                        .siteimg {
                                            width: 30%;
                                            aspect-ratio: 1/0.633333;
                                            // overflow: hidden;
                                            transition: transform 0.3s ease;
                                            &:hover {
                                                transform: scale(1.05);
                                            }
                                            img {
                                                width: 100%;
                                                height: 100%;
                                                object-fit: cover;
                                                object-position: center;
                                                cursor: pointer;
                                            }
                                            .imgdes {
                                                margin: 12px 0 36px 0;
                                                text-align: center;
                                            }
                                        }
                                    }
                                    // .on {
                                    //     display: block;
                                    // }
                                    // .off {
                                    //     display: none;
                                    // }
                                }
                                .caption1 {
                                    margin-bottom: 8px;
                                }
                                .outline {
                                    line-height: 22px;
                                }
                                .list {
                                    margin-bottom: 8px;
                                }
                                .flex {
                                    align-items: center;
                                    // font-weight: 500;
                                }
                                .fingericon {
                                    width: 24px;
                                    height: 24px;
                                    margin-right: 8px;
                                }
                                .accordion {
                                    margin: 0;
                                }
                                .select {
                                    line-height: 1.6;
                                }
                            }
                        }
                        // background-color: rgb(237, 216, 57);
                    }
                }
                .imgpopup {
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.95);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    z-index: 99999;
                    .popupcon {
                        background: #fff;
                        // padding: 20px;
                        border-radius: 10px;
                        position: relative;
                        // width: 60%;
                        // height: 65%;
                        width: 810px;
                        height: 470px;
                        // object-fit: fill;
                        // overflow: auto;
                        .popupimage {
                            // width: 100%;
                            // height: auto;
                            width: 100%;
                            height: 100%;
                            // aspect-ratio: 1/0.633333;
                            display: block;
                            border-radius: 8px;
                            object-fit: cover;
                            object-position: center top;
                        }
                        .closebutton {
                            // width: 32px;
                            // height: 32px;
                            all: unset;
                            position: absolute;
                            top: 10px;
                            right: 10px;
                            border: none;
                            // background: #000;
                            // color: white;
                            // border: none;
                            // padding: 5px 10px;
                            cursor: pointer;
                            // border-radius: 100%;
                            // font-size: 16px;
                            img {
                                border-radius: 100%;
                                border-color: #fff;
                                // width: 32px;
                                // height: 32px;
                                // top: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}
